import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './MainPage.scss';
import './MainPageSelect.scss';
import './MainPageSelect.js';
import SvgCart from '../../assest/svg/SvgCart.jsx';
import PizzaItem from '../../components/PizzaItem';
import PizzaCategories from '../../components/PizzaCategories.jsx';
import PizzaSort from '../../components/PizzaSort.jsx';
import JustTest from '../../components/JustTest.jsx';

let MainPage = ({ cartItems, cartItemsHandler }) => {

   // стейт с пиццами
   let [pizzaItems, pizzaItemsHandler] = useState([
      {
         pepperoni: {
            type: {
               all: true,
               meat: true,
               cheese: true,
            },
            name: 'Пепперони',
            img: 'pepperoni.jpeg',
            price: {
               low: 395,
               average: 639,
               high: 789,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         cheese: {
            type: {
               all: true,
               cheese: true,
               vegan: true,
            },
            name: 'Сырная 🌱',
            img: 'cheese.jpeg',
            price: {
               low: 245,
               average: 449,
               high: 589,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         ham_cheese: {
            type: {
               all: true,
               meat: true,
               cheese: true,
            },
            name: 'Ветчина и грибы',
            img: 'ham-cheese.jpeg',
            price: {
               low: 345,
               average: 539,
               high: 699,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         double_chicken: {
            type: {
               all: true,
               meat: true,
               cheese: true,
            },
            name: 'Двойной цыпленок',
            img: 'double-chicken.jpeg',
            price: {
               low: 295,
               average: 449,
               high: 589,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         cheese_chiken: {
            type: {
               all: true,
               meat: true,
               cheese: true,
            },
            name: 'Сырный цыпленок',
            img: 'cheese-chiken.jpg',
            price: {
               low: 395,
               average: 639,
               high: 789,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         mexican: {
            type: {
               all: true,
               meat: true,
               cheese: true,
               sharp: true,
            },
            name: 'Мексиканская 🌶️🌶️',
            img: 'mexican.jpg',
            price: {
               low: 445,
               average: 699,
               high: 849,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         pepperoni_fresh: {
            type: {
               all: true,
               meat: true,
               cheese: true,
            },
            name: 'Пепперони фреш',
            img: 'pepperoni-fresh.jpeg',
            price: {
               low: 245,
               average: 449,
               high: 589,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         four_cheese: {
            type: {
               all: true,
               cheese: true,
               vegan: true,
            },
            name: 'Четыре сыра 🌱',
            img: 'four-cheese.jpeg',
            price: {
               low: 445,
               average: 699,
               high: 849,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         veg_mush: {
            type: {
               all: true,
               cheese: true,
               vegan: true,
            },
            name: 'Овощи и грибы 🌱',
            img: 'veg-mush.jpeg',
            price: {
               low: 395,
               average: 639,
               high: 789,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
      {
         pizza_pie: {
            type: {
               all: true,
               vegan: true,
            },
            name: 'Пицца-пирог 🌱',
            img: 'pizza-pie.jpg',
            price: {
               low: 345,
               average: 539,
               high: 699,
            },
            size: {
               low: 25,
               average: 30,
               high: 35,
            },
            dough: {
               traditional: true,
               thin: true,
            }

         }
      },
   ])

   let [sortedPizza, sortedPizzaHandler] = useState([...pizzaItems])

   return (
      <div className="wrapper">
         {/* <JustTest /> */}
         <div className="header">
            <button className="header__login">Войти</button>
            <Link to="/pizza" className="header__logo">
               <img src='./images/pizza-peace.png' alt="pizza-logo" className="header__img" />
               <div className="header__logo-text">
                  <p className="header__title">REACT PIZZA</p>
                  <p className="header__subtitle">самая вкусная пицца</p>
               </div>
            </Link>
            <Link to={'/cart'} className="header__cart">
               {<SvgCart color={'#fff'} />}
               <p className="header__cart-number">0</p>
            </Link>
         </div>

         <div className="main">
            <div className="main__pizza main-pizza">
               <form onSubmit={(event) => event.preventDefault()}>
                  <div className="main-pizza__navigation">

                     <PizzaCategories
                        pizzaItems={pizzaItems}
                        sortedPizza={sortedPizza}
                        sortedPizzaHandler={sortedPizzaHandler}
                     />

                     <PizzaSort
                        pizzaItems={pizzaItems}
                        sortedPizza={sortedPizza}
                        sortedPizzaHandler={sortedPizzaHandler}
                     />
                  </div>
               </form>
               <p className="main-pizza__title">Пицца</p>
               <div className="main-pizza__content">
                  {
                     sortedPizza.map((item, index) => {
                        let name = Object.keys(item)[0]
                        return (
                           <PizzaItem key={name} pizza={item[name]} cartItems={cartItems} cartItemsHandler={cartItemsHandler} />
                        )
                     })
                  }
               </div>
            </div>
         </div>
      </div>
   )
}

export default MainPage
