
let MyButton = ({ children, clsName, active, ...props }) => {

   // для компонента PizzaCategories. Если пропс приходит с классом _active, то он вешается на элемент
   let activeClass = () => {
      if (active) {
         let newName = clsName + ' _active';
         return newName
      } else { return clsName }
   }

   return (
      <button className={activeClass()} {...props}> {children} </button>
   )
}

export default MyButton
