
import { Link } from 'react-router-dom';
import './CartPage.scss';
import SvgCart from '../../assest/svg/SvgCart.jsx';
import CartProducts from '../../components/CartProducts.jsx';

let CartPage = ({ cartItems, cartItemsHandler }) => {

   return (
      <div className="wrapper">
         <div className="header">
            <button className="header__login">Войти</button>
            <Link to="/pizza" className="header__logo">
               <img src='./images/pizza-peace.png' alt="pizza-logo" className="header__img" />
               <div className="header__logo-text">
                  <p className="header__title">REACT PIZZA</p>
                  <p className="header__subtitle">самая вкусная пицца</p>
               </div>
            </Link>
            <Link to={'/cart'} className="header__cart">
               {<SvgCart color={'#fff'} />}
               <p className="header__cart-number">0</p>
            </Link>
         </div>

         <div className="main">
            <div className="main-cart">
               {cartItems !== null
                  ? <CartProducts cartItems={cartItems} cartItemsHandler={cartItemsHandler} />
                  :
                  <div className="main-cart__empty">
                     <div className="main-cart__empty-text">
                        <p className="main-cart__empty-title">Корзина пустая <span>🙁</span></p>
                        <p className="main-cart__empty-subtle">Вероятнее всего, вы не добавили еще товар в коризну.</p>
                        <p className="main-cart__empty-subtle">Для того чтобы это сделать, перейдите на главную страницу</p>
                     </div>
                     <div className="main-cart__empty-picture">
                        <img src="./images/cart-person.png" alt={'cart-person'} />
                     </div>
                  </div>
               }
            </div>
         </div>
      </div>
   )
}

export default CartPage