import React, { useState, useEffect, useMemo } from 'react';
import CartItem from './CartItem.jsx';

let CartProducts = ({ cartItems, cartItemsHandler }) => {
   // общая цена всех питц
   let [pizzaTotal, pizzaTotalHandler] = useState({
      price: 0,
      quantity: 0,
   })

   let cartCount = 0
   let cartlPrice = 0

   // получаем общее количество заказанных товаров в корзине
   cartItems.forEach((elem) => {
      cartCount = cartCount + elem.totalCount
      cartlPrice += elem.totalPrice
   })

   useMemo(() => {
      pizzaTotalHandler({ ...pizzaTotal, quantity: cartCount, price: cartlPrice })
   }, [cartItems])

   // при загрузке сразуп прописывает общее количество и стоимость товаров в корзине
   useEffect(() => {
      pizzaTotalHandler({ ...pizzaTotal, quantity: cartCount, price: cartlPrice })
   }, [])

   return (
      <React.Fragment >
         <div className="main-cart__top">
            <div className="main-cart__title">
               <img src="./images/cart-icon-big.png" className="main-cart__title-icon" alt={'cart'} />
               <h1 className="main-cart__title-text">Корзина</h1>
            </div>
            <div className="main-cart__clear">
               <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M2.5 5H4.16667H17.5" stroke="#B6B6B6" strokeWidth="1.2" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M6.66663 5.00001V3.33334C6.66663 2.89131 6.84222 2.46739 7.15478 2.15483C7.46734 1.84227 7.89127 1.66667 8.33329 1.66667H11.6666C12.1087 1.66667 12.5326 1.84227 12.8451 2.15483C13.1577 2.46739 13.3333 2.89131 13.3333 3.33334V5.00001M15.8333 5.00001V16.6667C15.8333 17.1087 15.6577 17.5326 15.3451 17.8452C15.0326 18.1577 14.6087 18.3333 14.1666 18.3333H5.83329C5.39127 18.3333 4.96734 18.1577 4.65478 17.8452C4.34222 17.5326 4.16663 17.1087 4.16663 16.6667V5.00001H15.8333Z" stroke="#B6B6B6" strokeWidth="1.2" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M8.33337 9.16667V14.1667" stroke="#B6B6B6" strokeWidth="1.2" strokeLinecap="round" strokeLinejoin="round" />
                  <path d="M11.6666 9.16667V14.1667" stroke="#B6B6B6" strokeWidth="1.2" strokeLinecap="round" strokeLinejoin="round" />
               </svg>
               <p className="main-cart__clear-text">Очистить корзину</p>
            </div>
         </div>
         <form className="main-cart__form">

            <CartItem
               cartItems={cartItems}
               cartItemsHandler={cartItemsHandler}
               pizzaTotalHandler={pizzaTotalHandler}
               pizzaTotal={pizzaTotal}
            />

            <div className="main-cart__bottom">
               <div className="main-cart__info">
                  <p className="main-cart__total-products">Всего товаров: <span>{pizzaTotal.quantity} шт.</span></p>
                  <p className="main-cart__total-price">Сумма заказа <span>{pizzaTotal.price} ₽</span></p>
               </div>
               <div className="main-cart__buttons">
                  <button className="main-cart__back">Вернуться назад</button>
                  <button className="main-cart__pay">Оплатить заказ</button>
               </div>
            </div>
         </form>
      </React.Fragment>
   )
}

export default CartProducts