import React from 'react';
import MyLi from './UI/MyLi/MyLi.jsx';

let PizzaItemControl = ({ pizzaParametr, pizzaChangeDough }) => {

   let changeDough = (e) => {
      // вызывается в каждом пункте, если он совпадает с данными из стейта, то на него вешается класс
      if (e === pizzaParametr[0].dough) {
         return '_active'
      }
   }

   let changeSize = (e) => {
      if (e === pizzaParametr[1].size) {
         return '_active'
      }
   }

   return (
      <div className="pizza-item__select-container">
         <ul className="pizza-item__type">
            <MyLi
               onClick={(e) => pizzaChangeDough(e)}
               className={`pizza-item__type ${changeDough('тонкое')} `}
            >тонкое</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e)}
               className={`pizza-item__type ${changeDough('традиционное')} `}
            >традиционное</MyLi>

         </ul>

         <ul className="pizza-item__size">
            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'low')}
               className={`pizza-item__size ${changeSize('25 см.')} `}
            >25 см.</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'average')}
               className={`pizza-item__size ${changeSize('30 см.')} `}
            >30 см.</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'high')}
               className={`pizza-item__size ${changeSize('35 см.')} `}
            >35 см.</MyLi>
         </ul>
      </div>
   )
}

export default PizzaItemControl