import React, { useState } from 'react';
import './App.css';
import MainPage from './pages/MainPage/MainPage.jsx';
import CartPage from './pages/CartPage/CartPage.jsx';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';


function App() {
   let [cartItems, cartItemsHandler] = useState([])
   console.log(cartItems)

   return (
      <BrowserRouter>
         <Route path='/pizza'>
            <MainPage cartItems={cartItems} cartItemsHandler={cartItemsHandler} />
         </Route>

         <Route path='/cart'>
            <CartPage cartItems={cartItems} cartItemsHandler={cartItemsHandler} />
         </Route>

         <Redirect to='/pizza' />
      </BrowserRouter>
   )
}

export default App;
